.radio {
  position: relative;
  padding-left: 1.5em;
  user-select: none;
  font-size: 14px;
  cursor: pointer;
}

.radio input[type="radio"] {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.radio input[type="radio"]:checked + span::after {
  content: '';
  top: 4px;
  left: 4px;
  position: absolute;
  display: block;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background-color: #fff;
}

.radio input[type="radio"]:checked + span::before {
  border-color: #fbcf3a;
  background-color: #fbcf3a;
}

.radio span::before {
  content: '';
  border: 1px solid #dddddd;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  top: 0;
  left: 0;
  background-color: #fff;
  position: absolute;
  display: block;
}

.checkbox {
  position: relative;
  font-size: 14px;
  padding-left: 1.5em;
  user-select: none;
  cursor: pointer;
}

.checkbox input[type="checkbox"] {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.checkbox input[type="checkbox"]:checked + span::after {
  content: '';
  width: 0.6em;
  height: 0.3em;
  border-left: 0.05em solid #000;
  border-bottom: 0.05em solid #000;
  transform: rotate(-45deg);
  top: .3em;
  left: .15em;
  position: absolute;
  display: block;
}

.checkbox span::before {
  content: '';
  border: 1px solid #cecfd0;
  width: .9em;
  height: .9em;
  top: .1em;
  left: 0;
  background-color: #fff;
  position: absolute;
  display: block;
}

